import {
  ComponentProps,
  Streamlit,
  StreamlitComponentBase,
  withStreamlitConnection,
} from "streamlit-component-lib"
import React, { ReactNode } from "react"
import "./styles.css"

interface State {
  pageNumber: number
  clickPrevOrNext: boolean
  offset: number
}

/**
 * This is a React-based component template. The `render()` function is called
 * automatically when your component should be re-rendered.
 */
class Pagnation extends StreamlitComponentBase<State> {
  public constructor(props: ComponentProps) {
    super(props)

    this.state = {pageNumber: 1, clickPrevOrNext: false, offset: 0}
    Streamlit.setComponentValue(1)
  }

  public render = (): ReactNode => {
    // Arguments that are passed to the plugin in Python are accessible
    // via `this.props.args`. Here, we access the "name" arg.
    const pages = this.props.args["pages"]
    const max_pages = this.props.args["max"]
    const hvrclr = this.props.args["hvrclr"]
    const atvclr = this.props.args["atvclr"]

    // Show a button and some text.
    // When the button is clicked, we'll increment our "numClicks" state
    // variable, and send its new value back to Streamlit, where it'll
    // be available to the Python program.
    return (
      <span>
        <style>
          {
            `.btn:enabled:hover:not(.active) {
                background-color: ${hvrclr};
              }
             .btn.active {
                background: ${atvclr};
             }
             .btn:disabled {
                color: white;
             }`
          }
        </style>
        <div className="pagnation">
          <div>&nbsp;</div>
          <button className="btn" onClick={() => this.state.pageNumber > 1 ? 
                          this.onClickPrevOrNext(this.state.pageNumber - 1, max_pages) : 
                          this.onClickPrevOrNext(1, max_pages)}
                          disabled={this.state.pageNumber > 1 ? false : true}>
            Prev
          </button>
          {
            pages?.filter((number: number) => {
              if (max_pages > 0) {
                if (number > this.state.offset && number < this.state.offset + max_pages + 1) {

                  return number;
                }
              } else {
                return number;
              }
            }).map((number: number) =>
            <button className={this.state.pageNumber === number ? 'btn active' : 'btn'}
                    onClick={(e) => this.onClickNumber(number)}>
              {number}
            </button>
            )
          }
          <button className="btn"
                  onClick={() => this.state.pageNumber < pages[pages.length - 1] ? 
                          this.onClickPrevOrNext(this.state.pageNumber + 1, max_pages) : 
                          this.onClickPrevOrNext(pages[pages.length - 1], max_pages)}
                          disabled={this.state.pageNumber < pages[pages.length - 1] ? false : true}>
            Next
          </button>
        </div>
      </span>
    )
  }

  /** Click handler for our "Click Me!" button. */
  private onClickNumber = (number: number): void => {
    // Increment state.numClicks, and pass the new value back to
    // Streamlit via `Streamlit.setComponentValue`.
    this.setState(
      prevState => ({ pageNumber: number, clickPrevOrNext: false }),
      () => Streamlit.setComponentValue(this.state.pageNumber)
    )
  }

  private onClickPrevOrNext = (number: number, max: number): void => {
    // Increment state.numClicks, and pass the new value back to
    // Streamlit via `Streamlit.setComponentValue`.
    // 1 2 3 4 5
    // 2 3 4 5 6
    if (this.state.offset + max < number) {
      this.setState ({
        offset: this.state.offset + 1
      })
    } else if (this.state.offset + 1 > number && this.state.offset > 0) {
      this.setState({
        offset: this.state.offset - 1
      })
    }
    
    this.setState(
      prevState => ({ pageNumber: number, clickPrevOrNext: true}),
      () => {Streamlit.setComponentValue(this.state.pageNumber)}
    )
  }
}

// "withStreamlitConnection" is a wrapper function. It bootstraps the
// connection between your component and the Streamlit app, and handles
// passing arguments from Python -> Component.
//
// You don't need to edit withStreamlitConnection (but you're welcome to!).
export default withStreamlitConnection(Pagnation)
